import { Component, OnInit, Inject } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { MatDialog } from '@angular/material';

import { ProjectEditorService } from '../_services/project-editor.service';
import {
  Container,
  Card,
  UserRole, userRoleLabels,
  Requirement, RequirementType, requirementTypeLabels,
  Resource, ResourceType, resourceTypeLabels,
} from '../../../_models/_index';

import { ContainerLabelDialogComponent } from './container-label-dialog.component';
import { FieldOptionsDialogComponent } from './field-options-dialog.component';
import { ConfirmDeleteDialogComponent } from './confirm-delete-dialog.component';

@Component({
  selector: 'pe-project-editor',
  templateUrl: './project-editor.component.html',
  styleUrls: ['./project-editor.component.scss']
})
export class ProjectEditorComponent implements OnInit {

  userRoleList: { value: string, label: string }[];
  resourceTypeList: { value: string, label: string }[];
  requirementTypeList: { value: string, label: string }[];

  resourceTypeLabels = resourceTypeLabels;
  requirementTypeLabels = requirementTypeLabels;

  selectedStage: Container;
  selectedDesk: Container;
  selectedCard: Card;

  // Angular does not keep template reference in *ngIf statement alive after the statement,
  // so connecting this and new requirement fields with ngModel is workaround
  // UPD: propably deprecated commentary
  newResourceLabel: string;
  newRequirementTag: string;

  newResourceOptions: string[];

  constructor(
    private editor: ProjectEditorService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private router: Router,
  ) { }

  // For debugging
  log(...what: any[]): void {
    console.log(what);
  }

  ngOnInit() {
    this.userRoleList = [];
    for (const value of Object.keys(userRoleLabels)) {
      this.userRoleList.push({ value: value, label: userRoleLabels[value] });
    }
    this.resourceTypeList = [];
    for (const value of Object.keys(resourceTypeLabels)) {
      this.resourceTypeList.push({ value: value, label: resourceTypeLabels[value] });
    }
    this.requirementTypeList = [];
    for (const value of Object.keys(requirementTypeLabels)) {
      this.requirementTypeList.push({ value: value, label: requirementTypeLabels[value] });
    }
    this.route.paramMap.subscribe(params => {
      const id = params.get('id');
      this.editor.editExisting(id);
    });
    this.newResourceOptions = [];
  }

  save() {
    this.editor.save();
  }

  addStageDialog(): void {
    const dialog = this.dialog.open(ContainerLabelDialogComponent, {
      width: '400px',
      data: { siblings: this.editor.listStages() },
    });
    dialog.afterClosed().subscribe((data: { label: string, index: number } | string) => {
      if (undefined !== data && 'string' !== typeof data) {
        this.editor.addStage(this.editor.tagFromLabel(data.label), data.label, data.index);
      }
    });
  }

  deleteStageDialog(tag: string, label: string): void {
    const dialog = this.dialog.open(ConfirmDeleteDialogComponent, {
      width: '400px',
      data: { label: label },
    });
    dialog.afterClosed().subscribe((data: boolean | string) => {
      if (undefined !== data && 'string' !== typeof data) {
        this.editor.deleteStage(tag);
      }
    });
  }

  addDeskDialog(): void {
    const dialog = this.dialog.open(ContainerLabelDialogComponent, {
      width: '400px',
      data: { siblings: this.editor.listDesks(this.selectedStage.tag) },
    });
    dialog.afterClosed().subscribe((data: { label: string, index: number } | string) => {
      if (undefined !== data && 'string' !== typeof data) {
        this.editor.addDesk(this.editor.tagFromLabel(data.label), data.label, this.selectedStage.tag, data.index);
      }
    });
  }

  deleteDeskDialog(tag: string, label: string): void {
    const dialog = this.dialog.open(ConfirmDeleteDialogComponent, {
      width: '400px',
      data: { label: label },
    });
    dialog.afterClosed().subscribe((data: boolean | string) => {
      if (undefined !== data && 'string' !== typeof data) {
        this.editor.deleteDesk(tag);
      }
    });
  }

  addCardDialog(): void {
    const dialog = this.dialog.open(ContainerLabelDialogComponent, {
      width: '400px',
      data: { siblings: this.editor.listCards(this.selectedDesk.tag) },
    });
    dialog.afterClosed().subscribe((data: { label: string, index: number } | string) => {
      if (undefined !== data && 'string' !== typeof data) {
        this.editor.addCard(this.editor.tagFromLabel(data.label), data.label, this.selectedDesk.tag, data.index);
      }
    });
  }

  deleteCardDialog(tag: string, label: string): void {
    const dialog = this.dialog.open(ConfirmDeleteDialogComponent, {
      width: '400px',
      data: { label: label },
    });
    dialog.afterClosed().subscribe((data: boolean | string) => {
      if (undefined !== data && 'string' !== typeof data) {
        this.editor.deleteCard(tag);
      }
    });
  }

  selectStage(tag: string): void {
    this.selectedStage = this.editor.getStage(tag);
    this.selectedDesk = null;
    this.selectedCard = null;
  }

  selectDesk(tag: string): void {
    this.selectedDesk = this.editor.getDesk(tag);
    this.selectedCard = null;
  }

  selectCard(tag: string): void {
    this.selectedCard = this.editor.getCard(tag);
  }

  addResource(type: ResourceType): void {
    let resource: Resource; // TODO
    resource = {
      tag: this.editor.tagFromLabel(this.newResourceLabel + '_resource'),
      data_type: type,
      label: this.newResourceLabel,
    };
    if ('op' === type || 'ch' === type) {
      resource.options = this.newResourceOptions;
    }
    this.editor.addResource(this.selectedCard.tag, resource);
    this.newResourceLabel = '';
    this.newResourceOptions = [];
  }

  deleteResource(tag: string): void {
    this.editor.deleteResource(tag);
  }

  addRequirement(type: RequirementType): void {
    let requirement: Requirement; // TODO
    if ('work_finished' === type) {
      if (!this.newRequirementTag) { // TODO
        return;
      }
      requirement = {
        tag: this.editor.tagFromLabel(this.newRequirementTag + '_requirement'),
        requirement_type: type,
        observables: [this.newRequirementTag],
      };
    }
    this.editor.addRequirement(this.selectedCard.tag, requirement);
    this.newRequirementTag = '';
  }

  deleteRequirement(tag: string): void {
    this.editor.deleteRequirement(tag);
  }

  cardHasRequirement(tag: string): boolean {
    for (const requirement_tag of this.selectedCard.requirements) {
      if (requirement_tag === tag) {
        return true;
      }
    }
    return false;
  }

  goToCard(tag: string): void {
    this.selectedCard = this.editor.getCard(tag);
    [this.selectedStage, this.selectedDesk] = this.editor.getCardPath(tag);
  }

  editResourceOptions(tag?: string): void {
    let resource: Resource;
    if (undefined !== tag) {
      resource = this.editor.getResource(tag);
    }
    const dialog = this.dialog.open(FieldOptionsDialogComponent, {
      width: '400px',
      data: { options: resource ? resource.options : this.newResourceOptions },
    });
    dialog.afterClosed().subscribe((data: string[] | string) => {
      if ('string' !== typeof data) {
        if (undefined !== resource) {
          resource.options = data;
        } else {
          this.newResourceOptions = data;
        }
      }
    });
  }

  moveStageUp(): void {
    if (undefined === this.selectedStage) {
      return;
    }
    const i = this.editor.stageIndex(this.selectedStage.tag);
    this.editor.moveStage(this.selectedStage.tag, i - 1);
  }

  moveStageDown(): void {
    if (undefined === this.selectedStage) {
      return;
    }
    const i = this.editor.stageIndex(this.selectedStage.tag);
    this.editor.moveStage(this.selectedStage.tag, i + 1);
  }

  deleteStage(): void {
    if (undefined === this.selectedStage) {
      return;
    }
    this.deleteStageDialog(this.selectedStage.tag, this.selectedStage.label);
    this.selectedStage = undefined;
  }

  moveDeskUp(): void {
    if (undefined === this.selectedDesk) {
      return;
    }
    const i = this.editor.deskIndex(this.selectedDesk.tag);
    this.editor.moveDesk(this.selectedDesk.tag, i - 1);
  }

  moveDeskDown(): void {
    if (undefined === this.selectedDesk) {
      return;
    }
    const i = this.editor.deskIndex(this.selectedDesk.tag);
    this.editor.moveDesk(this.selectedDesk.tag, i + 1);
  }

  deleteDesk(): void {
    if (undefined === this.selectedDesk) {
      return;
    }
    this.deleteDeskDialog(this.selectedDesk.tag, this.selectedDesk.label);
    this.selectedDesk = undefined;
  }

  moveCardUp(): void {
    if (undefined === this.selectedCard) {
      return;
    }
    const i = this.editor.cardIndex(this.selectedCard.tag);
    this.editor.moveCard(this.selectedCard.tag, i - 1);
  }

  moveCardDown(): void {
    if (undefined === this.selectedCard) {
      return;
    }
    const i = this.editor.cardIndex(this.selectedCard.tag);
    this.editor.moveCard(this.selectedCard.tag, i + 1);
  }

  deleteCard(): void {
    if (undefined === this.selectedCard) {
      return;
    }
    this.deleteCardDialog(this.selectedCard.tag, this.selectedCard.label);
    this.selectedCard = undefined;
  }

}




