import { move } from 'typescript-array-utils';
import { Observable } from 'rxjs/Observable';

import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';

import { HttpService } from '../../http/_services/http.service';

import {
  Project,
  Container,
  Card,
  Resource,
  Requirement,
} from '../../../_models/_index';

@Injectable()
export class ProjectEditorService {

  private project: Project;
  private stages: { [tag: string]: Container };
  private desks: { [tag: string]: Container };
  private cards: { [tag: string]: Card };
  private resources: { [tag: string]: Resource };
  private requirements: { [tag: string]: Requirement };

  constructor(private http: HttpService) { }

  listProjects(): Observable<{ id: number, label: string }[]> {
    return this.http.get('project_template/list');
  }

  createNew(label: string): Observable<{ id: number }> {
    return this.http.post<{ id: number }>('project_template/create', { label: label });
  }

  editExisting(id: string): void {
    this.project = { label: null, children: [] };
    this.stages = {};
    this.desks = {};
    this.cards = {};
    this.resources = {};
    this.requirements = {};
    this.http.get<{
      project: Project,
      stages: Container[],
      desks: Container[],
      // TODO: Rename card to work
      works: Card[],
      resources: Resource[],
      requirements: Requirement[],
    }>('project_template/get', {
      params: new HttpParams().append('id', id),
    }).subscribe(data => {
      this.project = data.project;
      for (const i of data.stages) {
        this.stages[i.tag] = i;
      }
      for (const i of data.desks) {
        this.desks[i.tag] = i;
      }
      for (const i of data.works) {
      // TODO: Rename card to work
        this.cards[i.tag] = i;
      }
      for (const i of data.resources) {
        this.resources[i.tag] = i;
        if ('ch' === i.data_type || 'op' === i.data_type) {
          this.resources[i.tag].options = i.text_data.options;
        }
      }
      for (const i of data.requirements) {
        this.requirements[i.tag] = i;
      }
    });
  }

  save(): void {
    const body = {
      project: this.project,
      stages: this.stages,
      desks: this.desks,
      works: this.cards,
      resources: this.resources,
      requirements: this.requirements,
    };
    for (const tag of Object.keys(body.resources)) {
      body.resources[tag].text_data = { options: body.resources[tag].options };
      delete body.resources[tag].options;
    }
    this.http.post('project_template/update', body).subscribe();
  }

  private goodTag(tag: string): void {
    if ('' === tag) {
      throw RangeError("Empty tags is not allowed");
    }
    if (tag in this.stages
      || tag in this.desks
      || tag in this.cards
      || tag in this.resources
      || tag in this.requirements) {
      throw RangeError("Tag '" + tag + "' alreaty exists");
    }
  }

  private goodLabel(label: string): void {
    if ('' === label) {
      throw RangeError("Label cannot be empty string");
    }
  }

  // Not used everywhere it supposed to be
  private getAncestorTags(tag: string): {
    stage?: string,
    desk?: string,
    card?: string,
    resource?: string,
    requirement?: string,
  } {
    for (const stage_tag of this.project.children) {
      if (stage_tag === tag) {
        return { stage: stage_tag };
      }
      for (const desk_tag of this.stages[stage_tag].children) {
        if (desk_tag === tag) {
          return { stage: stage_tag, desk: desk_tag };
        }
        for (const card_tag of this.desks[desk_tag].children) {
          if (card_tag === tag) {
            return { stage: stage_tag, desk: desk_tag, card: card_tag };
          }
          for (const requirement_tag of this.cards[card_tag].requirements) {
            if (requirement_tag === tag) {
              return { stage: stage_tag, desk: desk_tag, card: card_tag, requirement: requirement_tag };
            }
          }
          for (const resource_tag of this.cards[card_tag].resources) {
            if (resource_tag === tag) {
              return { stage: stage_tag, desk: desk_tag, card: card_tag, resource: resource_tag };
            }
          }
        }
      }
    }
    return undefined;
  }

  tagFromLabel(label: string): string {
    this.goodLabel(label);
    let tag = label;
    let count = 0;
    while (tag in this.stages
      || tag in this.desks
      || tag in this.cards
      || tag in this.resources
      || tag in this.requirements) {
      count = count + 1;
      tag = label + '_' + count;
    }
    return tag;
  }

  addStage(tag: string, label: string, at: number): void {
    this.goodTag(tag);
    this.goodLabel(label);
    const stage = { tag: tag, label: label, children: [] };
    this.stages[tag] = stage;
    this.project.children.splice(at, 0, tag);
  }

  getStage(tag: string): Container {
    return this.stages[tag];
  }

  renameStage(tag: string, label: string): void {
    this.goodLabel(label);
    this.stages[tag].label = label;
  }

  moveStage(tag: string, at: number): void {
    for (const i in this.project.children) {
      if (tag === this.project.children[i]) {
        this.project.children = move(this.project.children, +i, at);
      }
    }
  }

  deleteStage(tag: string): void {
    for (const desk_tag of this.stages[tag].children) {
      this.deleteDesk(desk_tag);
    }
    delete this.stages[tag];
    for (const i in this.project.children) {
      if (tag === this.project.children[i]) {
        this.project.children.splice(+i, 1);
      }
    }
  }

  listStages(): { tag: string, label: string }[] {
    const list: { tag: string, label: string }[] = [];
    for (const tag of this.project.children) {
      list.push({ tag: tag, label: this.stages[tag].label });
    }
    return list;
  }

  stageIndex(tag: string): number {
    for (const i in this.project.children) {
      if (this.project.children[i] === tag) {
        return +i;
      }
    }
    return undefined;
  }

  addDesk(tag: string, label: string, stage_tag: string, at: number): void {
    this.goodTag(tag);
    this.goodLabel(label);
    const desk = { tag: tag, label: label, children: [] };
    this.desks[tag] = desk;
    this.stages[stage_tag].children.splice(at, 0, tag);
  }

  getDesk(tag: string): Container {
    return this.desks[tag];
  }

  renameDesk(tag: string, label: string): void {
    this.goodLabel(label);
    this.desks[tag].label = label;
  }

  moveDesk(tag: string, at: number): void {
    const tags = this.getAncestorTags(tag);
    if (undefined === tags) {
      return;
    }
    const stage_tag = tags.stage;
    for (const i in this.stages[stage_tag].children) {
      if (tag === this.stages[stage_tag].children[i]) {
        this.stages[stage_tag].children = move(this.stages[stage_tag].children, +i, at);
      }
    }
  }

  deleteDesk(tag: string): void {
    for (const card_tag of this.desks[tag].children) {
      this.deleteCard(card_tag);
    }
    delete this.desks[tag];
    for (const _tag of this.project.children) {
      for (const i in this.stages[_tag]) {
        if (tag === this.stages[_tag].children[i]) {
          this.stages[_tag].children.splice(+i, 1);
        }
      }
    }
  }

  listDesks(tag: string): { tag: string, label: string }[] {
    const list: { tag: string, label: string }[] = [];
    for (const _tag of this.stages[tag].children) {
      list.push(this.desks[_tag]);
    }
    return list;
  }

  deskIndex(tag: string): number {
    const tags = this.getAncestorTags(tag);
    if (undefined === tags) {
      return undefined;
    }
    const stage = this.stages[tags.stage];
    for (const i in stage.children) {
      if (stage.children[i] === tag) {
        return +i;
      }
    }
  }

  addCard(tag: string, label: string, desk_tag: string, at: number): void {
    this.goodTag(tag);
    this.goodLabel(label);
    const card: Card = {
      tag: tag,
      label: label,
      executor_text: null,
      verifier_text: null,
      executor_role: null,
      verifier_role: null,
      duration: null,
      resources: [],
      requirements: [],
      is_goal: false,
    };
    this.cards[tag] = card;
    this.desks[desk_tag].children.splice(at, 0, tag);
  }

  getCard(tag: string): Card {
    return this.cards[tag];
  }

  moveCard(tag: string, at: number): void {
    const tags = this.getAncestorTags(tag);
    if (undefined === tags) {
      return;
    }
    const desk_tag = tags.desk;
    for (const i in this.desks[desk_tag].children) {
      if (tag === this.desks[desk_tag].children[i]) {
        this.desks[desk_tag].children = move(this.desks[desk_tag].children, +i, at);
      }
    }
  }

  deleteCard(tag: string): void {
    for (const resource_tag of this.cards[tag].resources) {
      this.deleteRequirement(resource_tag);
    }
    for (const requirement_tag of this.cards[tag].requirements) {
      this.deleteRequirement(requirement_tag);
    }
    delete this.cards[tag];
    for (const _tag of this.project.children) {
      for (const __tag of this.stages[_tag].children) {
        for (const i in this.desks[__tag].children) {
          if (tag === this.desks[__tag].children[i]) {
            this.desks[__tag].children.splice(+i, 1);
          }
        }
      }
    }
  }

  cardIndex(tag: string): number {
    const tags = this.getAncestorTags(tag);
    if (undefined === tags) {
      return undefined;
    }
    const desk = this.desks[tags.desk];
    for (const i in desk.children) {
      if (desk.children[i] === tag) {
        return +i;
      }
    }
  }

  listCards(tag: string): { tag: string, label: string }[] {
    const list: { tag: string, label: string }[] = [];
    for (const _tag of this.desks[tag].children) {
      list.push(this.cards[_tag]);
    }
    return list;
  }

  listAllCards(): { tag: string, label: string }[] {
    const list: { tag: string, label: string }[] = [];
    for (const tag of Object.keys(this.cards)) {
      list.push(this.cards[tag]);
    }
    return list;
  }

  addResource(tag: string, resource: Resource): void {
    this.goodTag(resource.tag);
    this.resources[resource.tag] = resource;
    this.cards[tag].resources.push(resource.tag);
  }

  getResources(tag: string): Resource[] {
    const resources = [];
    for (const _tag of this.cards[tag].resources) {
      resources.push(this.resources[_tag]);
    }
    return resources;
  }

  getResource(tag: string): Resource {
    return this.resources[tag];
  }

  deleteResource(tag: string): void {
    const tags = this.getAncestorTags(tag);
    if (undefined !== tags && 'resource' in tags) {
      for (const i in this.cards[tags.card].resources) {
        if (tag === this.cards[tags.card].resources[i]) {
          this.cards[tags.card].resources.splice(+i, 1);
        }
      }
    }
    delete this.resources[tag];
  }

  addRequirement(tag: string, requirement: Requirement): void {
    this.goodTag(requirement.tag);
    this.requirements[requirement.tag] = requirement;
    this.cards[tag].requirements.push(requirement.tag);
  }

  getRequirements(tag: string): Requirement[] {
    const requirements = [];
    for (const _tag of this.cards[tag].requirements) {
      requirements.push(this.requirements[_tag]);
    }
    return requirements;
  }

  getRequirement(tag: string): Requirement {
    return this.requirements[tag];
  }

  deleteRequirement(tag: string): void {
    const tags = this.getAncestorTags(tag);
    if (undefined !== tags && 'requirement' in tags) {
      for (const i in this.cards[tags.card].requirements) {
        if (tag === this.cards[tags.card].requirements[i]) {
          this.cards[tags.card].requirements.splice(+i, 1);
        }
      }
    }
    delete this.requirements[tag];
  }

  listAllPossibleParents(tag: string): { tag: string, label: string }[] {
    const list: { tag: string, label: string }[] = [];
    for (const _tag of Object.keys(this.cards)) {
      let isChild = false;
      for (const requirement_tag of this.cards[tag].requirements) {
        if ('work_finished' === this.requirements[requirement_tag].requirement_type
          && this.requirements[requirement_tag].observables.includes(_tag)) {
          isChild = true;
          break;
        }
      }
      if (tag !== _tag && !isChild) {
        list.push(this.cards[_tag]);
      }
    }
    return list;
  }

  listCardChildren(tag: string): { tag: string, label: string }[] {
    const list: { tag: string, label: string }[] = [];
    for (const _tag of Object.keys(this.cards)) {
      for (const requirement_tag of this.cards[_tag].requirements) {
        if ('work_finished' === this.requirements[requirement_tag].requirement_type
          && this.requirements[requirement_tag].observables.includes(tag)) {
          list.push({ tag: _tag, label: this.cards[_tag].label });
        }
      }
    }
    return list;
  }

  getCardPath(tag: string): [Container, Container] {
    let desk: Container;
    for (const _tag of Object.keys(this.desks)) {
      for (const card of this.desks[_tag].children) {
        if (tag === card) {
          desk = this.desks[_tag];
        }
      }
    }
    let stage: Container;
    for (const _tag of Object.keys(this.stages)) {
      for (const _desk of this.stages[_tag].children) {
        if (desk.tag === _desk) {
          stage = this.stages[_tag];
        }
      }
    }
    return [ stage, desk ];
  }

  canSave(): boolean {
    return true;
  }

}




