import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'pe-confirm-delete-dialog',
  template: `
  <mat-dialog-content>
    <h3>В действительно хотите удалить <b>{{data.label}}</b></h3>
  </mat-dialog-content>
  <mat-dialog-actions>
    <button mat-button (click)="confirmDelete()">Удалить</button>
    <button mat-button mat-dialog-close>Отменить</button>
  </mat-dialog-actions>
  `,
})
export class ConfirmDeleteDialogComponent {
  constructor(
    private dialogRef: MatDialogRef<ConfirmDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { label: string },
  ) { }

  confirmDelete() {
    this.dialogRef.close(true);
  }

}



