import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

import { siteUrl } from '../http.config';

@Injectable()
export class AuthenticationService {
  constructor(private http: HttpClient) { }

  login(phone: string, password: string): Observable<string> {
    return this.http.post<{ token: string }>(siteUrl + 'api-token-auth', { phone: phone, password: password })
    .map(response => {
      if (response && response.token) {
        localStorage.setItem('helToken', response.token);
      }
      return response.token;
    });
  }

  logout() {
    localStorage.removeItem('helToken');
  }
}



