import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'pe-container-label-dialog',
  template: `
  <mat-dialog-content>
  <form>
    <mat-form-field>
      <input matInput #name placeholder="Наименование">
    </mat-form-field>
    <mat-select #position placeholder="Добавить..." [value]="data.siblings.length">
      <mat-option *ngFor="let stage of data.siblings; let i = index;" [value]="i">
        ... перед "{{stage.label}}"
      </mat-option>
      <mat-option [value]="data.siblings.length">
        ... в конец
      </mat-option>
    </mat-select>
  </form>
  </mat-dialog-content>
  <mat-dialog-actions>
    <button mat-button (click)="confirmLabel(name.value, position.value)">Добавить</button>
    <button mat-button mat-dialog-close>Отменить</button>
  </mat-dialog-actions>
  `,
})
export class ContainerLabelDialogComponent {

  constructor(
    private dialogRef: MatDialogRef<ContainerLabelDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { siblings: { tag: string, label: string }[] },
  ) { }

  confirmLabel(label: string, index: number): void {
    if ('' !== label && null !== index) {
      this.dialogRef.close({ label: label, index: index });
    }
  }

}



