import { Component, Input } from '@angular/core';

@Component({
  selector: 'pm-chat-message',
  template: `
  <div class="d-flex justify-content-space-between align-items-end"
  [class.my]="my">
    <ng-content select="pm-chat-message-author"></ng-content>
    <span class="p-2 mat-elevation-z2">
      <ng-content select="pm-chat-message-content"></ng-content>
    </span>
  </div>
  `,
  styles: [
    `
    :host {
      position: relative;
      z-index: -10;
      margin: 15px;
    }
    `,
    `
    .my {
      flex-direction: row-reverse;
    }
    `,
  ]
})
export class ChatMessageComponent {
  @Input()
  public my = false;

  constructor() { }

}



