import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ProjectManagerComponent } from './project-manager/project-manager.component';
import { ProjectListComponent } from './project-list/project-list.component';
import { StageListComponent } from './stage-list/stage-list.component';
import { CardListComponent } from './card-list/card-list.component';
import { CardComponent } from './card/card.component';

const routes: Routes = [
  {
    path: 'manager',
    component: ProjectManagerComponent,
    children: [
      { path: '', pathMatch: 'full', component: ProjectListComponent },
      { path: ':id/stages', pathMatch: 'full', component: StageListComponent },
      { path: ':tag/cards', pathMatch: 'full', component: CardListComponent },
      { path: ':tag', pathMatch: 'full', component: CardComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProjectManagerRoutingModule { }



