import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { MaterialModule } from '../material/material.module';
import { HttpModule } from '../http/http.module';

import { ProjectEditorRoutingModule } from './project-editor-routing.module';

import { ProjectEditorService } from './_services/project-editor.service';

import { ProjectListComponent } from './project-list/project-list.component';
import { ProjectLabelDialogComponent } from './project-list/project-label-dialog.component';
import { ProjectEditorComponent } from './project-editor/project-editor.component';
import { ContainerLabelDialogComponent } from './project-editor/container-label-dialog.component';
import { FieldOptionsDialogComponent } from './project-editor/field-options-dialog.component';
import { ConfirmDeleteDialogComponent } from './project-editor/confirm-delete-dialog.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,

    MaterialModule,
    HttpModule,

    ProjectEditorRoutingModule
  ],
  declarations: [
    ProjectListComponent,
    ProjectLabelDialogComponent,
    ProjectEditorComponent,
    ContainerLabelDialogComponent,
    FieldOptionsDialogComponent,
    ConfirmDeleteDialogComponent,
  ],
  entryComponents: [
    ProjectLabelDialogComponent,
    ContainerLabelDialogComponent,
    FieldOptionsDialogComponent,
    ConfirmDeleteDialogComponent,
  ],
  providers: [
    ProjectEditorService,
  ]
})
export class ProjectEditorModule { }



