import { Injectable } from "@angular/core";

import {
  HttpClient,
  HttpHandler,
  HttpRequest,
  HttpParams,
  HttpHeaders,
  HttpResponse,
  HttpEvent,
} from "@angular/common/http";

import { Router } from '@angular/router';

import { Observable } from "rxjs/Observable";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';

import { apiUrl } from '../http.config';

@Injectable()
export class HttpService {
  constructor(
    private http: HttpClient,
    private router: Router,
  ) { }

  request<T>(method: string, url: string, options?: {
    body?: any;
    headers?: HttpHeaders;
    observe?: 'body';
    params?: HttpParams;
    responseType?: 'json';
    reportProgress?: boolean;
    withCredentials?: boolean;
  }): Observable<T> {
    return this.http.request<T>(method, apiUrl + url, this.addJwt(options)).catch(this.handleError);
  }

  delete<T>(url: string, options?: {
    headers?: HttpHeaders;
    observe?: 'body';
    params?: HttpParams;
    reportProgress?: boolean;
    responseType?: 'json';
    withCredentials?: boolean;
  }): Observable<T> {
    return this.http.delete<T>(apiUrl + url, this.addJwt(options)).catch(this.handleError);
  }

  get<T>(url: string, options?: {
    headers?: HttpHeaders;
    observe?: 'body';
    params?: HttpParams;
    reportProgress?: boolean;
    responseType?: 'json';
    withCredentials?: boolean;
  }): Observable<T> {
    return this.http.get<T>(apiUrl + url, this.addJwt(options)).catch(this.handleError);
  }

  post<T>(url: string, body: any | null, options?: {
    headers?: HttpHeaders;
    observe?: 'body';
    params?: HttpParams;
    reportProgress?: boolean;
    responseType?: 'json';
    withCredentials?: boolean;
  }): Observable<T> {
    return this.http.post<T>(apiUrl + url, body, this.addJwt(options)).catch(this.handleError);
  }

  put<T>(url: string, body: any | null, options?: {
    headers?: HttpHeaders;
    observe?: 'body';
    params?: HttpParams;
    reportProgress?: boolean;
    responseType?: 'json';
    withCredentials?: boolean;
  }): Observable<T> {
    return this.http.put<T>(apiUrl + url, body, this.addJwt(options)).catch(this.handleError);
  }

  private addJwt(options?: {
    body?: any;
    headers?: HttpHeaders;
    observe?: 'body';
    params?: HttpParams;
    reportProgress?: boolean;
    responseType?: 'json';
    withCredentials?: boolean;
  }): {
    body?: any;
    headers?: HttpHeaders;
    observe?: 'body';
    params?: HttpParams;
    reportProgress?: boolean;
    responseType?: 'json';
    withCredentials?: boolean;
  } {
    const token = localStorage.getItem('helToken');
    if (token) {
      if (undefined === options) {
        options = { headers: new HttpHeaders() };
      }
      if (!('headers' in options)) {
        options.headers = new HttpHeaders();
      }
      options.headers = options.headers.append('Authorization', 'JWT ' + token);
    }

    return options;
  }

  private handleError(error: any) {
    if (401 === error.status) {
      window.location.href = '/login';
    }

    return Observable.throw(error._body);
  }
}



