import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'pe-field-options-dialog',
  template: `
  <mat-dialog-content>
    <form>
      <div *ngFor="let opt of options; let index = index;">
        <mat-form-field>
          <input matInput [value]="opt" (change)="setOption(index, $event)">
        </mat-form-field>
        <button mat-icon-button (click)="deleteOption(index)"><mat-icon>highlight_off</mat-icon></button>
      </div>
    </form>
    <div>
      <button mat-icon-button (click)="addOption()" class="mx-auto"><mat-icon>add</mat-icon></button>
    </div>
  </mat-dialog-content>
  <mat-dialog-actions>
    <button mat-button (click)="confirmOptions()">Подтвердить</button>
    <button mat-button mat-dialog-close>Отменить</button>
  </mat-dialog-actions>
  `,
})
export class FieldOptionsDialogComponent implements OnInit {

  options: string[];

  constructor(
    private dialogRef: MatDialogRef<FieldOptionsDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { options: string[] },
  ) { }

  ngOnInit() {
    this.options = this.data.options;
  }

  addOption(): void {
    this.options.push('');
  }

  setOption(index: number, $event: any): void {
    this.options[index] = $event.target.value;
  }

  deleteOption(index: number): void {
    this.options.splice(index, 1);
  }

  confirmOptions(): void {
    this.dialogRef.close(this.options);
  }

}



