import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'pe-project-label-dialog',
  template: `
  <mat-dialog-content>
  <form class="full-width">
    <mat-form-field class="full-width">
      <input matInput #name placeholder="Имя шаблона">
    </mat-form-field>
  </form>
  </mat-dialog-content>
  <mat-dialog-actions>
    <button mat-button (click)="confirmLabel(name.value)">Создать</button>
    <button mat-button mat-dialog-close>Отменить</button>
  </mat-dialog-actions>
  `,
})
export class ProjectLabelDialogComponent {

  constructor(
    private dialogRef: MatDialogRef<ProjectLabelDialogComponent>,
  ) { }

  confirmLabel(label: string): void {
    if ('' !== label) {
      this.dialogRef.close(label);
    }
  }

}



