import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { MatDialog } from '@angular/material';

import { ProjectEditorService } from '../_services/project-editor.service';

import { ProjectLabelDialogComponent } from './project-label-dialog.component';

@Component({
  selector: 'pe-project-list',
  templateUrl: './project-list.component.html',
  styleUrls: ['./project-list.component.scss']
})
export class ProjectListComponent implements OnInit {

  project_list: { id: number, label: string }[];

  constructor(
    private editor: ProjectEditorService,
    private router: Router,
    private dialog: MatDialog,
  ) { }

  ngOnInit() {
    this.editor.listProjects().subscribe(list => {
      this.project_list = list;
    },
    error => {
      console.log(error);
    });
  }

  createNew() {
    const dialog = this.dialog.open(ProjectLabelDialogComponent, {
      width: '400px',
    });
    dialog.afterClosed().subscribe((label: string) => {
      if (undefined !== label && '' !== label) {
        this.editor.createNew(label).subscribe(response => {
          const id = response.id;
          this.router.navigate(['editor', id ]);
        });
      }
    });
  }

}



