import {
  Component,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef,
  OnInit,
  AfterViewChecked
} from '@angular/core';

@Component({
  selector: 'pm-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit, AfterViewChecked {
  @Output()
  public postMessage = new EventEmitter<string>();
  @ViewChild('messageWrapper')
  private messageWrapper: ElementRef;

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewChecked() {
    this.scrollBottom();
  }

  scrollBottom() {
    this.messageWrapper.nativeElement.scrollTop =
      this.messageWrapper.nativeElement.scrollHeight;
  }

  send(message: string) {
    this.postMessage.emit(message);
  }


}

export { ChatMessageComponent } from './chat-message.component';
export { ChatMessageContentComponent } from './chat-message-content.component';
export { ChatMessageAuthorComponent } from './chat-message-author.component';



